<script>
export let wndw,dcmnt;
import Router from './components/Router.tmp.svelte';
import Menu from './components/Toc.svelte';
import Page from './components/Page.svelte';
import Header from './components/Header.svelte';
let page;
let colC=4
let colL=2
let MenuV="visible"
function wide(){colC=6;colL=1;}
function readUrlQuery(){
  var queryString = wndw.location.search;
  var queryObject = new Object();
  if(queryString){
    queryString = queryString.substring(1);
    var parameters = queryString.split('&');

    for (var i = 0; i < parameters.length; i++) {
      var element = parameters[i].split('=');

      var paramName = decodeURIComponent(element[0]);
      var paramValue = decodeURIComponent(element[1]);

      queryObject[paramName] = paramValue;
  }
}
if(queryObject["wide"]!=null){colC=6;colL=1;
MenuV="hidden";
}
}
readUrlQuery()
</script>
<Router bind:page={page} wndw={wndw}/>
<span><a href="/x.a.b.c">x.a.b.c</a></span>
<Header/>

<div class="grid grid-cols-8 gap-4">
  <div style="visibility:{MenuV}"  class="col-span-{colL}"><Menu /></div>
  <div class="col-span-{colC}"><Page page={page}>abcdefdf</Page> </div>
  <div class="col-span-1"></div>
</div>
<Header asHeader={false}/>